
.coverpage {
    background-image: url("Images/BI.jpg");
    position: fixed ;
    background-size: cover;
    min-height: 100%;
    min-width: 100%;
}

.cptext {
    color: antiquewhite;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 700;
    font-size: 30px;
    padding-top: 16%;
    padding-bottom: 8px;
    text-align: center;
    width: 500px;
    margin-left: 30%;
    animation-name: fade;
    animation-duration:5s;

}
@keyframes fade{
    from {opacity:0}
    to {opacity:1}
}

.cptext_1 {
    color: antiquewhite;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 400;
    font-size: 18px;
    padding: 10px;
    text-align: center;
    cursor: pointer;
    background: red;
    width: 100px;
    margin-left: 44%;
    animation-name: fade;
    animation-duration: 3s;
}
